import Swiper from "swiper";

export default () => {
  const projectList = document.querySelector(".project-list");
  if (projectList) {
    return new Swiper(".swiper-container", {
      slidesPerView: "auto",
      pagination: {
        el: ".swiper-pagination",
        type: "bullets"
      },
      breakpoints: {
        690: {
          slidesPerView: 1,
          spaceBetween: 0
        },

        1020: {
          slidesPerView: 2,
          spaceBetween: 20
        }
      }
    });
  }
};
