import throttle from "lodash/throttle";
import init from "../_utils/plugin-init";
import scrollTo from "@/modules/_utils/scrollTo";
import getOffsetTop from "@/modules/_utils/dom/getOffsetTop";

class ScrollTo {
  constructor(element, options) {
    this.element = element;
    this.name = "scroll-to";

    this._defaults = {
      offsetAttr: "data-scroll-offset", // number or "center"
      duration: 400
    };

    this.options = {
      ...this._defaults,
      ...options
    };

    this.init();
  }

  init() {
    this.buildCache();
    this.bindEvents();
    this.updateViewportHeight();
  }

  getViewportHeight() {
    return parseInt(document.documentElement.clientHeight, 10);
  }

  updateViewportHeight() {
    this.viewportHeight = this.getViewportHeight();
  }

  buildCache() {
    this.viewportHeight = 0;
    this.offset = this.element.getAttribute(this.options.offsetAttr);
  }

  bindEvents() {
    window.addEventListener(
      "resize",
      throttle(this.updateViewportHeight.bind(this), 150)
    );

    this.element.addEventListener("click", event => this.handleClick(event));
  }

  isLink(el) {
    return el.hasAttribute("href") && el.getAttribute("href").length;
  }

  handleClick(event) {
    event.preventDefault();

    if (!this.isLink(this.element)) return;

    const hash = this.element.getAttribute("href");
    const target = document.querySelector(hash);
    let offset = 0;

    if (target) {
      const targetTop = getOffsetTop(target);

      if (this.offset === "center") {
        const targetHeight = parseInt(target.offsetHeight, 10);

        if (targetHeight < this.viewportHeight) {
          offset = Math.ceil((this.viewportHeight - targetHeight) / 2);
        }
      } else {
        offset = parseInt(this.offset, 10);
      }

      let options = { duration: this.options.duration };
      const to = targetTop - offset;

      scrollTo({ to, options });
    }
  }
}

export default init(ScrollTo);
