import scrollTo from "@/modules/_utils/scrollTo";
import getOffsetTop from "@/modules/_utils/dom/getOffsetTop";
import toArray from "@/modules/_utils/dom/toArray";

export default () => {
  const mainMenu = document.querySelector(".main-menu");
  if (mainMenu) {
    const links = toArray(mainMenu.querySelectorAll('a[href^="#"]'));
    const duration = 400;
    const offset = 0;

    links.forEach(link => {
      link.addEventListener("click", event => {
        event.preventDefault();

        const hash = link.getAttribute("href");
        const target = document.querySelector(hash);

        if (target) {
          let options = { duration };
          const to = getOffsetTop(target) - offset;

          scrollTo({ to, options });

          if (history.pushState) {
            history.pushState(null, null, hash);
          } else {
            location.hash = hash;
          }
        }
      });
    });
  }
};
